import React from 'react'
import { graphql } from 'gatsby'
import { GatsbyImage } from "gatsby-plugin-image";
import Layout from "/src/layouts/main/layout/layout.js";
import Seo from "/src/components/seo"

import "/src/assets/scss/templates/project.scss"

const ProjectTemplate = ({ data }) => {
  const  project = data.markdownRemark
  /* Usar imagen en el top con parallax */
  const { description, title, frontimage, date } = project.frontmatter
  const { slug } = project.fields
  const content = project.html
  
  return (
    <Layout>
      <div className="project-content">
        <Seo />
        <h1>{title}</h1>
        { frontimage ? <GatsbyImage image={frontimage.childImageSharp?.gatsbyImageData} alt="" className="project-item__img" /> : '' }
        <section className='project-body' 
            dangerouslySetInnerHTML={{ __html: content }}
        ></section>
      </div>
    </Layout>
  )
  
}

export default ProjectTemplate

export const query = graphql`query ProjectBySlug(
    $id: String!
    $previousPostId: String
    $nextPostId: String
  ) {
    site {
      siteMetadata {
        title
      }
    }
    markdownRemark(
        id: { eq: $id }
      ) {
      id
      excerpt(pruneLength: 160)
      html
      frontmatter {
        title
        start_date(formatString: "MMMM DD, YYYY")
        frontimage {
            childImageSharp {
                gatsbyImageData(width: 960, height: 400, layout: CONSTRAINED)
            }
          }
        description
      }
      fields {
        slug
      }
    }
    previous: markdownRemark(id: { eq: $previousPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
    next: markdownRemark(id: { eq: $nextPostId }) {
      fields {
        slug
      }
      frontmatter {
        title
      }
    }
  }
`